.alien-scoreboard {
  width: 90vw;
  margin: auto;
}
.alien_scoreboard-container {
  width: 96vw;
  margin: auto;
}

.alien-scoreboardTable {
  width: 80vw;
  text-align: center;
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  font-family: sans-serif;
  min-width: 400px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

.alien-scoreboardTable th,
.alien-scoreboardTable td {
  padding: 12px 15px;
}
.alien-scoreboardTable > tr {
  background-color: #009879;
  color: #ffffff;
  border-bottom: 1px solid #dddddd;
}

.alien-scoreboardTable > td {
  border: 1px solid black;
}

.alien-scoreboardTable tr:nth-of-type(even) {
  background-color: #f3f3f3;
  color: black;
}

.alien-scoreboardTable tr:last-of-type {
  border-bottom: 2px solid #009879;
}
