.alientyping_container {
  height: auto;
  width: 100%;
  background-image: linear-gradient(
      365deg,
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0.8)
    ),
    url("../images/hero2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: left;
  place-items: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: rgba(255, 255, 255, 0.87);
}

@media (max-width: 760px) {
  .alientyping_container {
    font-size: 90%;
  }
  .alientyping_container p {
    width: 80% !important;
  }

  .main_container > div > p {
    width: 86%;
  }
  .alientyping_container h4 {
    font-size: 1rem;
  }
  .alientyping_container a {
    font-size: 18px;
  }
}

.alien_heading {
  color: rgb(163, 106, 0);
  font-size: 3rem;
  font-variant: small-caps;
  font-weight: 900;
  margin-bottom: 10vh;
}
.main_container > div {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.main_container > div > p {
  margin-left: 1rem;
}

.alientyping_container > * {
  padding-bottom: 4rem;
}

.icon_keyboard {
  font-size: 30px;
}

.alientyping_container button {
  border: none;
  padding: 0.5rem 1.4rem;
  border-radius: 0.3rem;
  margin-left: 2rem;
  background-color: black;
  color: white;
  border-radius: 1rem;
}
