.header {
  margin: 0;
  padding: 0;
  min-height: 94vh;
  display: flex;
  display: grid;
  grid-template-columns: 6fr 0.1fr;
  width: 100%;
  /* margin-top: 2rem; */
  margin-top: -2rem;
  line-height: 1.5;
}

.header_container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding-left: 4rem;
  /* flex: 1; */
}
.header_left {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90vh;

  /* display: none; */
}
.header .combinedCircle {
  height: 60%;
  object-fit: contain;
  align-items: flex-end;
}

/* @keyframes coimbinedcircle {
  from {
    transform: translateX(50px);
  }
  to {
    transform: translateX(100px);
  }
} */

.header .circle_orange {
  height: 1.4rem;
  width: 1.4rem;
  border-radius: 50%;
  background-color: #e84118;
  position: absolute;
  margin-top: -30vh;
  animation: movecircle 3.5s infinite;
}

@keyframes movecircle {
  from {
    transform: translateX(0px);
    opacity: 0.2;
  }
  to {
    transform: translateX(250px);
    opacity: 1;
  }
}
.header .header_innercontainer {
  padding-left: 1.8vw;
}

.header_left img {
  /* display: block; */
  height: 700px;
  min-width: 850px;
  object-fit: contain;
}

.header .header_innercontainer h2 {
  font-size: 3.3rem;
  font-weight: 700;
  font-family: "Times New Roman", Times, serif;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: #e84118;
  margin-bottom: 2rem;
}

.header .colorNone {
  color: #0e2769;
}

.header_shortDescp {
  font-size: 16px;
  color: #031953;
  margin: 2rem auto;
}

.header_buttonGrps {
  display: flex;
  justify-content: space-around;
  max-width: 20vw;
}

.header_buttonGrps button {
  padding: 0.5rem 1.4rem;
  border-radius: 0.3rem;
}

@media (max-width: 760px) {
  .header .header_innercontainer h2 {
    font-size: 2rem;
    width: 70vw;
  }
}

.header_btn1 {
  border: none;
  background: linear-gradient(300deg, #7f00ff, #e100ff);
  color: white;
  font-weight: 700;
  cursor: pointer;
  transition: border, color 0.4s ease-in-out;
}
.header_btn1 a {
  color: white;
}

.header_btn2 {
  border: 2px solid #e84118;
  transition: all 0.4s ease-in-out;
}
.header_btn1:hover {
  background: white;
  color: #9c2ecf;
  border: 2px solid #9c2ecf;
}

.header_btn1:hover a {
  color: #9c2ecf;
}

.header_btn2:hover {
  background-color: #e84118;
  color: white;
}
