@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,500&display=swap");
/* @import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,500&family=Sawarabi+Mincho&display=swap"); */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: poppins;
  /* font-family: "Sawarabi Mincho", sans-serif; */
}

body {
  background-color: rgba(255, 255, 255, 0.904);
  line-height: 1.3;
  /* background-color: #141414; */
}

.timerMain {
  margin-bottom: 3rem;
}

.eventMain {
  width: 90vw;
  margin: auto;
  display: grid;
  /* grid-template-columns: repeat(auto-fill, minmax(300, 1fr)); */
  grid-template-columns: repeat(auto-fit, minmax(600px, 1fr));
}
.logo_container {
  background-image: linear-gradient(
      365deg,
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0.8)
    ),
    url("./images/hero1.jpg");
}

.mainHeader {
  background-color: red;
  background: linear-gradient(-300deg, #7f00ff, #e100ff);
  position: sticky;
  min-width: 100%;
  color: white;
}

.mainHeader div h3 {
  padding-left: 3rem;
}
