.animate_icons {
  transition: all 0.5s ease-in-out;
}

.event_container:hover .animate_icons {
  -webkit-animation: animateIcon 700ms infinite ease;
          animation: animateIcon 700ms infinite ease;
}

@-webkit-keyframes animateIcon {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

@keyframes animateIcon {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  50% {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
  }
  100% {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

