.eventadmin {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.eventadmin:hover .top:before,
.eventadmin:hover .top:after,
.eventadmin:hover .bottom:before,
.eventadmin:hover .bottom:after,
.eventadmin:active .top:before,
.eventadmin:active .top:after,
.eventadmin:active .bottom:before,
.eventadmin:active .bottom:after {
  margin-left: 200px;
  -webkit-transform-origin: -200px 50%;
  transform-origin: -200px 50%;
  transition-delay: 0s;
}

.eventadmin:hover .center,
.eventadmin:active .center {
  opacity: 1;
  transition-delay: 0.2s;
}

.top:before,
.top:after,
.bottom:before,
.bottom:after {
  content: "";
  display: block;
  position: absolute;
  width: 200vmax;
  height: 200vmax;
  top: 50%;
  left: 50%;
  margin-top: -100vmax;
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  z-index: 10;
  opacity: 0.65;
  transition-delay: 0.2s;
}

.top:before {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background: #e46569;
}

.top:after {
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
  background: #ecaf81;
}

.bottom:before {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  background: #60b8d4;
}

.bottom:after {
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  background: #3745b5;
}

.center {
