.alterLogo_container {
  min-height: 100vh;
  width: 100%;
  background-color: #ebecf0;
  overflow: hidden;
  display: grid;
  place-items: center;
}

.loader_component {
  position: absolute;
  height: 100vh;
  display: flex;
  justify-content: center;
  width: 100%;
  align-items: center;
}

.alterlogo_form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 70vw;
  padding: 16px;
  margin: 0 auto;
}

.alterlogo_form button,
.alterlogo_form input {
  border: 0;
  outline: 0;
  font-size: 16px;
  border-radius: 3rem;
  padding: 16px;
  background-color: #ebecf0;
  text-shadow: 1px 1px 0 #fff;
  margin-bottom: 2rem;
}

.alterlogo_form input {
  box-shadow: inset 2px 2px 5px #babecc, inset -5px -5px 10px #fff;
  width: 70%;
  box-sizing: border-box;
  transition: all 0.2s ease-in-out;
  appearance: none;
  -webkit-appearance: none;
}

.alterlogo_form input:focus {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}

.alterlogo_form button {
  color: #61677c;
  font-weight: bold;
  box-shadow: -5px -5px 20px #fff, 5px 5px 20px #babecc;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
  font-weight: 600;
}

.alterlogo_form button:hover {
  box-shadow: -2px -2px 5px #fff, 2px 2px 5px #babecc;
}
.alterlogo_form button:active {
  box-shadow: inset 1px 1px 2px #babecc, inset -1px -1px 2px #fff;
}

.student_logoImage {
  height: 150px;
  width: 300px;
  object-fit: contain;
}
