.eventadmin {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.eventadmin:hover .top:before,
.eventadmin:hover .top:after,
.eventadmin:hover .bottom:before,
.eventadmin:hover .bottom:after,
.eventadmin:active .top:before,
.eventadmin:active .top:after,
.eventadmin:active .bottom:before,
.eventadmin:active .bottom:after {
  margin-left: 200px;
  -webkit-transform-origin: -200px 50%;
  transform-origin: -200px 50%;
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
}

.eventadmin:hover .center,
.eventadmin:active .center {
  opacity: 1;
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}

.top:before,
.top:after,
.bottom:before,
.bottom:after {
  content: "";
  display: block;
  position: absolute;
  width: 200vmax;
  height: 200vmax;
  top: 50%;
  left: 50%;
  margin-top: -100vmax;
  -webkit-transform-origin: 0 50%;
  transform-origin: 0 50%;
  -webkit-transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  z-index: 10;
  opacity: 0.65;
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
}

.top:before {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  background: #e46569;
}

.top:after {
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
  background: #ecaf81;
}

.bottom:before {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  background: #60b8d4;
}

.bottom:after {
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
  background: #3745b5;
}

.center {
  position: absolute;
  width: 400px;
  height: 400px;
  top: 50%;
  left: 50%;
  margin-left: -200px;
  margin-top: -200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: flex-start;
  padding: 30px;
  opacity: 0;
  -webkit-transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  transition: all 0.5s cubic-bezier(0.445, 0.05, 0, 1);
  -webkit-transition-delay: 0s;
  transition-delay: 0s;
  color: #333;
}

.center input {
  width: 100%;
  padding: 15px;
  margin: 5px;
  border-radius: 1px;
  border: 1px solid #ccc;
  font-family: inherit;
}

.eventadmin input {
  border: none;
  outline: none;
  border-bottom: 2px solid grey;
  transition: all 500ms ease;
  width: 75%;
}

.eventadmin input:active,
.eventadmin input:focus {
  border-bottom: 2px solid red;
  width: 90%;
}

.center button {
  margin-top: 1.3rem;
  margin-left: 4rem;
  padding: 0.3rem 1rem;
  outline: none;
  background: white;
  border: 1px solid green;
  color: green;
  border-radius: 2rem;
  cursor: pointer;
  transition: all 500ms ease;
}

.center button:hover {
  border: none;
  background-color: rgb(7, 202, 7);
  color: white;
}
