.event_container {
  display: flex;
  justify-content: left;
  align-items: center;
  font-size: 16px;
  margin: 2.5rem 0;
  color: #031953;
}

.animate_icons {
  transition: all 0.5s ease-in-out;
}

.event_container:hover .animate_icons {
  animation: animateIcon 700ms infinite ease;
}

@keyframes animateIcon {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(0.9);
  }
}

.event_right {
  display: flex;
  flex-direction: column;
  margin-left: 2.3rem;
}

.event_right .event_details {
  display: flex;
  align-items: center;
}

.event_details > * {
  padding-right: 1rem;
}
.event_innerdetails {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.event_innerdetails > * {
  padding-left: 0.4rem;
}
.event_innerdetails h3 {
  font-size: 14px;
}
.event_info {
}
.event_originalName {
  font-weight: 900;
  font-size: 1.3rem;
}
.event_info > * {
  margin: 0.6rem 0;
}

.event_right p {
  width: 80%;
  letter-spacing: 0.4px;
  line-height: 1.5;
  text-align: justify;
}

.event_name {
  color: #e84118;
}

.event_innerdetails {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (max-width: 760px) {
  .event_container {
    max-width: 100%;
    font-size: 80%;
  }
  .event_innerdetails > * {
    padding: 0;
  }
  .event_details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50vw;
  }
  .event_details > * {
    margin-bottom: 0.5rem;
  }
  .animate_icons {
    font-size: 1.9rem !important;
  }
  .event_info {
    width: 60%;
  }
}
