.logoItem_container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  place-items: center;
  grid-gap: 1.3rem;
}

.logo_header {
}
.logoHeader_innercontainer {
  justify-content: center;
  align-items: center;
  /* background-color: red; */
  margin-bottom: 3rem;
}

.logoHeader_innercontainer > * {
  margin-right: 4rem;
}

.logoItem_innerContainer {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.logoItem_container img {
  width: 250px;
  height: 150px;
  object-fit: contain;
}
